.bar {
    background-color: #1c1f24;
    color: white;
    padding: 20px;
    display: flex;
    flex-direction: row-reverse;
    position: relative;
    transition: all 500ms;
}
#switch:checked ~ * .bar {
  background-color: #dbdee3;
}

.bar input.field {
    background-color: #dbdbe3;
    border: none;
    border-radius: 5px;
    padding: 5px;
    margin-right: 20px;
    margin-top: 0px;
    font-size: 18px;
    transition: all 500ms;
}
#switch:checked ~ * .bar input.field {
  background-color: white;
}

.bar input.field::placeholder {
    color: #9d93ac;
}

.search {
    width: 150px;
    background-color: #7137c8;
    color: white;
    border-radius: 5px;
    padding: 5px;
    border: none;
    font-size: 18px;
    transition: all 500ms;
    transition: all 500ms;
}

.search:hover {
    background-color: #8d5fd3;
}

.toggle {
    position: absolute;
    top: 10px;
    left: 10px;
    width: 40px;
    height: 20px;
    border-radius: 20px;
    background: #5d536c;
    display: flex;
    align-items: center;
    transition: all 500ms;
}

.circle {
    height: 16px;
    width: 16px;
    border-radius: 50%;
    background: #1c1f24;
    margin-left: 22px;
    transition: all 500ms;
}
#switch:checked ~ * .circle {
  background: #dbdbe3;
  margin-left: 3px;
}

.mode {
    position: absolute;
    top: 40px;
    left: 10px;
    font-size: 0.9em;
    color: #7c6f91;
    transition: all 500ms;
}
